@font-face {
  font-family: "oswald";
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "oswald";
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
html {
  height: 100%;
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  margin: 0;
  padding: 0;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}
.button {
  display: block;
  text-align: center;
  text-decoration: none;
  color: #ffffff;
  background-color: #68b738;
  cursor: pointer;
}
.button:hover {
  background: #5EAA2F;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
input[type='number'] {
  -moz-appearance: textfield;
}
.container {
  padding-left: 20px;
  padding-right: 20px;
}
@media (min-width: 768px) {
  .container {
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (min-width: 1440px) {
  .container {
    margin: 0 auto;
    padding-left: 30px;
    padding-right: 30px;
    max-width: 1280px;
  }
}
.title-line {
  border-top: 2px solid #68B738;
}
.form .title-line-wrap,
.catalog .title-line-wrap {
  margin-top: -17px;
}
@media (min-width: 768px) {
  .form .title-line-wrap,
  .catalog .title-line-wrap {
    margin-top: -24px;
  }
}
@media (min-width: 1440px) {
  .form .title-line-wrap,
  .catalog .title-line-wrap {
    margin-top: -24px;
    padding-left: 40px;
    padding-right: 40px;
  }
}
.page__header {
  padding-top: 11px;
}
.header__top-block {
  position: relative;
}
.header__block-logo {
  align-self: flex-start;
  padding: 0 20px 16px;
  border-bottom: 1px solid #e6e6e6;
}
.block-logo__link {
  display: block;
  font-size: 0;
  width: fit-content;
}
.block-logo__link[href]:hover {
  opacity: 0.8;
}
.block-logo__link[href]:active {
  opacity: 0.6;
}
.main-nav__toggle {
  display: block;
  position: absolute;
  top: 13px;
  right: 20px;
  padding: 0;
  width: 24px;
  height: 18px;
  border: none;
  cursor: pointer;
  background-color: #ffffff;
}
.header__main-nav--nojs .main-nav__toggle {
  display: none;
}
.header__main-nav .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
.header__main-nav .main-nav__toggle--close::before,
.header__main-nav .main-nav__toggle--close::after {
  content: "";
  position: absolute;
  top: 8px;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: none;
}
.header__main-nav .main-nav__toggle--close::before {
  transform: rotate(45deg);
}
.header__main-nav .main-nav__toggle--close::after {
  transform: rotate(-45deg);
}
.main-nav__list {
  display: none;
  flex-direction: column;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-nav__list--open,
.header__main-nav--nojs .main-nav__list {
  display: flex;
}
.main-nav__item {
  text-align: center;
  border-bottom: 1px solid #e6e6e6;
}
.main-nav__item:hover {
  opacity: 0.6;
}
.main-nav__item:active {
  opacity: 0.3;
}
.main-nav__link {
  display: block;
  padding-top: 22px;
  padding-bottom: 23px;
  font-family: oswald, Verdana, sans-serif;
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
  text-transform: uppercase;
  text-decoration: none;
  color: #000000;
}
@media (min-width: 768px) {
  .header__top-block {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    padding: 13px 30px 37px 30px;
  }
  .header__block-logo {
    padding: 0;
    border: none;
  }
  .block-logo__link img {
    width: 174px;
    height: 52px;
  }
  .main-nav__toggle {
    display: none;
  }
  .main-nav__list {
