  }
  .header__top-block {
    margin: 0 auto;
    padding: 39px 30px 32px;
    max-width: 1280px;
  }
  .block-logo__link img {
    width: 202px;
    height: 59px;
  }
  .main-nav__list {
    min-width: 478px;
  }
  .page .main-nav__item--active {
    border-bottom: 2px solid #ffffff;
  }
  .main-nav__item--active {
    border-bottom: 2px solid #68b738;
  }
  .page .main-nav__link {
    font-family: oswald, Verdana, sans-serif;
    font-size: 20px;
    line-height: 30px;
    font-weight: 400;
    text-transform: uppercase;
    color: #ffffff;
  }
  .main-nav__link {
    font-family: oswald, Verdana, sans-serif;
    font-size: 20px;
    line-height: 30px;
    font-weight: 400;
    text-transform: uppercase;
  }
